// Body
//
// The main body of the page.

.page {
  @extend .uikit-body;
  a {
    border-bottom: 1px solid $colour-Secondary;
    &:hover {
      text-decoration: none;
      background: none;
    }
    &:focus {
      background-color: $colour-Secondary-light;
      color: white;
    }
  }
}